/* Все стили для заголовков */
.title {
  font-size: 32px;
  font-weight: 800;
  line-height: 44px;

  margin: 0;
  margin-bottom: 30px;
  padding: 0;

  text-align: center;

  color: rgb(0, 0, 0);
}

.title--main {
  font-size: 50px;
  font-weight: 800;
  line-height: 58px;

  margin: 0;
  padding: 0;

  color: #fff;
}

.title--white {
  font-size: 32px;
  font-weight: 700;
  line-height: 40px;

  margin: 0;
  padding: o;

  color: #fff;
}

.manufacture-stairs__title {
  padding-top: 40px;
}


@media (max-width: 1200px) {
  .title--main {
    font-size: 35px;
    line-height: 36px;
    margin-top: 50px;
  }

  .title--white {
    font-size: 23px;
    line-height: 26px;
  }
}


// Страница "Наши выполненные работы"
.our-work__title {
  color: #cb895b;
  color: #fff;
}

.black--title {
  color: #fff;
}

@media (max-width: 641px) {
  .title--white {
    font-size: 19px;
    line-height: 21px;
  }
}


@media (max-width: 630px) {
  .title {
    font-size: 28px;
    line-height: 30px;
  }
}

@media (max-width: 500px) {
  .title {
    font-size: 24px;
    line-height: 26px;
    padding-top: 15px;
  }

  .title--main {
    font-size: 24px;
    line-height: 26px;

  }
}
