/* стилизация страницы КОНТАКТЫ*/
.contacts-page__header {
  background-color: #271915;
  background-image: url(../img/background-dark-wood--2.png);

}


.contacts-wrap {
  max-width: 100%;
}

.contacts-page__title {
  margin-bottom: 32px;
}

.contacts-page__text {
  font-size: 16px;
  color: #ffffff;
  font-weight: 500;
  line-height: 24px;
}

.contacts-page__text--bold {
  font-weight: 700;
  font-size: 16px;
  color: #ffffff;
  line-height: 24px;
}

.contacts-page__content {
  background-color: #EEE8E2;
  position: relative;
}

.contacts-page__wrapper {
  max-width: 1170px;
  margin: 0 auto;
  padding: 115px;
  padding-left: 0;
}

.contacts-page__map {
  background-image: url(../img/map-adress.png);
  position: absolute;
  top: 0;
  right: 0;
  width: 75%;
  height: 100%;
}

.cottact-details {
  padding: 20px 78px 100px;
  background-color: #fff;
  color: #000;
  max-width: 400px;
  position: relative;
}

.cottact-details__title {
  font-size: 32px;
  line-height: 37px;
  font-weight: 700;
}

.ymaps-2-1-72-controls__toolbar,
.ymaps-2-1-72-copyright,
.ymaps-2-1-72-map-copyrights-promo,
.ymaps-2-1-72-controls__control {
  display: none !important;
}

.cottact-details a {
  list-style: none;
  color: #000;
  font-style: inherit;
  text-decoration: none;

}


@media (max-width: 1281px) {
  .cottact-details {
    margin-left: 45px;
  }

  .contacts-page__title {
    margin-bottom: 60px;
  }
}

@media (max-width: 770px) {
  .cottact-details {
    padding: 15px 43px 29px;
    max-width: 325px;
  }

  .contacts-page__wrapper {
    padding: 25px;
  }

  .contacts-page__map {
    width: 100%;
  }

  .contacts-page__text {
    font-size: 14px;
    line-height: 17px;
    margin: 0;
  }
}

@media (max-width: 420px) {
  .cottact-details__title {
    font-size: 18px;
    line-height: 22px;
  }

  .cottact-details__text {
    font-size: 12px;
    line-height: 17px;
  }

  .contacts-page__map {
    display: none;
  }

  .contacts-page__wrapper {
    padding: 0;
  }

  .cottact-details {
    margin: 0;
  }
}
