/* стилизация фотогалереи на странице "Наши работы"*/
.photos-works {
  background-image: url(../img/background-dark-wood.png);
  background-repeat: repeat-x;
  background-position: 0 0;
  margin-top: -1px;
  padding-bottom: 80px;
}

.photos-works__list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.photos-works__item {
  margin-bottom: 70px;
  margin-top: 45px;
}

.photo-work__wrap {
  position: relative;
  max-width: 357px;
}

.photo-work__picture {
  width: 100%;
}

.photo-work__description {
  width: 328px;
  display: block;
  background-color: #eee8e2;
  position: absolute;
  top: 55%;
  width: 90%;
  margin: 0 20px;
  text-align: center;
}

.photo-work__title {
  color: #000000;
  font-size: 18px;
  line-height: 24px;
  font-weight: 700;
  padding: 0 30px;
  margin-top: 30px;

}

.photo-work__text {
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  padding: 0 30px;
  margin-top: 20px;
  margin-bottom: 15px;
}

.photo-work__button {
  width: 100%;
}

@media (max-width: 770px) {
  .photo-work__title {
    font-size: 18px;
    line-height: 24px;
    font-weight: 700;
    padding: 0px 10px;
    margin-top: 20px;
  }

  .photo-work__text {
    font-size: 12px;
    line-height: 16px;
    padding: 0 10px;
    margin-top: 15px;
    margin-bottom: 10px;
  }

  .photo-work__button {
    font-weight: 600;
    margin: 0 auto;
    padding: 15px 25px;
  }

  .photo-work__description {
    margin: 0 12px;
  }

}

@media (max-width: 640px) {
  .photos-works {

    background-image: none;
  }

  .photo-work__description {
    top: 70%;
  }

  .photo-work__description {
    margin: 0px 13px;
  }

  .photo-work__title {
    font-size: 12px;
    line-height: 15px;
  }

  .photo-work__text {
    display: none;
  }

  .photo-work__button {
    padding: 16px;
  }

}

@media (max-width: 630px) {
  .photo-work__description {
    margin: 0 8px;
  }

  .photo-work__title {
    padding: 0px 5px;
    margin-top: 10px;
  }

  .photo-work__button {
    padding: 10px;
    font-size: 11px;
  }

  .photo-work__description {
    top: 65%;
  }
}
