/* стилизация блока услуги*/
.services {
  display: flex;

  flex-wrap: wrap;
  justify-content: center;
}

.services__item {
  position: relative;

  width: 21%;
  margin-bottom: 50px;
  padding: 15px;
}

.services__item img {
  width: 100%;
}

.services_bonus {
  position: absolute;
  top: 14px;
  left: 17%;

  display: block;

  margin: 0 auto;
  padding: 13px 16px;

  text-align: center;
  text-transform: uppercase;

  color: #fff;
  background-color: #cb895b;
}

.services__name {
  font-size: 16px;
  line-height: 24px;

  position: absolute;
  bottom: -15px;
  width: 75%;
  left: 5%;

  display: block;

  padding: 20px 18px;

  text-align: center;

  color: #000;
  background-color: #fff;

}

.services__price {
  font-size: 20px;
  font-weight: 800;
  line-height: 22px;

  display: block;
}

.services__wrap {
  margin: 0;
  padding: 0;
  position: relative;
}

@media (max-width: 1040px) {
  .services__wrap img {
    width: 100%;
  }

  .services__name {
    left: 6%;
    padding: 13px 17px;
  }

  .services_bonus {
    left: 15%;
    padding: 10px 10px;
  }

  .services {
    margin-bottom: 133px;
  }

}

@media (max-width: 800px) {
  .services__item {
    width: 45%;
  }

  .services_bonus {
    font-size: 10px;
    line-height: 12px;
  }

  .services__name {
    font-size: 12px;
    line-height: 14px;
  }

  .services__price {
    font-size: 14px;
    line-height: 18px;
  }

  .services__wrap::before {
    display: none;
  }

}

@media (max-width: 630px) {

  .services_bonus {
    left: 13%;
    font-size: 7px;
    line-height: 9px;
  }

  .services__name {
    font-size: 8px;
    line-height: 11px;
  }

  .services__price {
    font-size: 10px;
    line-height: 14px;
  }

}

@media (max-width: 500px) {
  .services {
    margin-bottom: 50px;
  }

  .services__item {
    width: 80%;
  }

  .services__name {
    font-size: 13px;
    line-height: 15px;
    background-color: #cb895b;
    color: #fff;
    width: 96%;
    position: absolute;
    bottom: 0;
    margin: 0;
    padding: 15px 5px;
    left: 0;
  }
}

@media (max-width: 320px) {

  .services {
    margin-bottom: 35px;
  }

}
