// прочие услуги три колонки
.bonus-services {
  background-image: url(../img/background-white-wood.png);
  background-repeat: repeat-x;
  margin-top: -76px;
  padding-top: 135px;
  padding-bottom: 80px;
}


.bonus-service__list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.bonus-service__item {
  max-width: 264px;
  margin-bottom: 20px;
}

.bonus-service__picture {
  width: 100%;
}

.bonus-service__wrap {
  max-width: 235px;
  text-align: center;
  position: relative;
  margin: 0 auto;
  margin-top: -50px;

}

.bonus-service__text {
  max-width: 380px;
  padding: 12px 10px;
  background-color: #fff;
  color: #000;
  font-size: 13px;
  line-height: 17px;

}

.bonus-service__title--2 {
  margin: 0;
  font-size: 18px;
  font-weight: 700;
  line-height: 24px;
}

.bonus-service__button {
  width: 100%;
  margin: 0;
  padding: 16px 20px;
  font-size: 12px;

}
