/* стилизация модальных окон */
.modal {
  position: fixed;
  z-index: 10;
  top: 90px;
  left: 50%;

  display: none;

  width: 360px;
  margin: 0 auto;
  margin-left: -240px;
  padding: 40px 50px;
  padding-bottom: 30px;

  transition: all .3s ease-in-out;

  color: #000;
  background-color: #fff;
  box-shadow: 0 30px 50px rgba(0, 0, 0, .7);
}

.modal__title {
  font-family: 'Montserrat';
  font-size: 32px;
  font-weight: bold;
  line-height: 41px;

  color: rgb(0, 0, 0);
}

.pop-up__form {
  max-width: 320px;
  margin: 0 auto;
}

.pop-up-form__field {
  width: 75%;
  margin-bottom: 20px;
  padding: 23px 43px;

  color: #828282;
  border: 2px solid #828282;
}

.modal__button-closed {
  font-size: 0;

  position: absolute;
  top: 27px;
  right: 27px;

  width: 44px;
  height: 44px;

  cursor: pointer;

  border: 0;
  background-color: #fff;
  background-image: url(../img/modal-close-btm.svg);
  background-repeat: no-repeat;
  background-size: contain;
}

/* Мишкины стили */
button,
a,
input {
  outline: none;
}

.top-navigation__button_active~.top-navigation__list {
  max-height: 1000px;
  transition: 2s;
  animation: show 1s 1;
  animation-fill-mode: forwards;
  animation-delay: 1s;
}

.fancybox-close-small {
  display: none !important;
}

.fancybox-content {
  padding: 0px !important;
}

.fancybox-content .modal {
  position: relative;
  top: 0px;
  left: 0px;
  max-width: 460px;
  margin-left: 0px;
  width: auto;
}

.fancybox-content .modal .pop-up-form__field {
  width: 100%;
}

.top-navigation__list {
  display: flex !important;
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -ms-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  overflow: hidden;
  max-height: 0px;
  padding: 0 0;
}

.top-navigation__list .top-navigation__item {
  padding: 5px 0;
}

@media (max-width: 500px) {
  .modal__title {
    font-size: 19px;
    line-height: 24px;
  }

  .modal__text {
    margin: 0;
    margin-bottom: 10px;
  }

  .pop-up-form__field {
    margin-bottom: 10px;
    padding: 14px;
    border: 1px solid #828282;
  }

  .modal__button-closed {
    top: 15px;
    right: 12px;
    width: 30px;
    height: 30px;
  }

}
