// блок по видам основных услуг (в две колонки)
.basic-service {
  background-image: url(../img/background-white-wood--2.png);

  background-position-y: 252px;
  background-repeat: repeat-x;
}

.basic-service__list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;

}

.basic-service__item {
  max-width: 556px;
  margin-bottom: 40px;

}

.basic-service__picture {
  width: 100%;
}

.basic-service__wrap {
  max-width: 500px;
  text-align: center;
  position: relative;
  margin: 0 auto;
  margin-top: -110px;

}

.basic-service__text {
  max-width: 440px;
  padding: 15px 30px;
  background-color: #fff;
  color: #000;

}

.basic-service__button {
  width: 100%;
  margin: 0;
  padding: 20px 0px;
  display: block;

}


@media (max-width: 800px) {
  .basic-service__list {
    display: block;
  }

  .basic-service__item {
    width: 100%;
  }

  .basic-service__wrap {
    width: 100%;
  }
}
