/* стилизация блока на главной странице "Лучшие работы"*/
.best-works {
  display: flex;
  flex-direction: column;

  margin-bottom: 80px;

  justify-content: center;
  overflow-x: hidden;
}

.best-works__title {
  padding-top: 75px;
}

.best-works__list {
  margin: 0 auto;
  padding: 0;

  list-style: none;
}

.work-item {
  margin: 0 auto;
}

.work-item_text {
  font-size: 18px;
  font-weight: 700;
  line-height: 22px;

  display: block;

  width: 370px;
  margin: 0 auto;
  margin-left: 40px;
  padding: 31px 65px;

  background-color: #eee8e2;
}

.work-item_text--sm {
  font-size: 14px;
  font-weight: 500;
  line-height: 22px;

  display: block;
}

.work-item__link {
  width: 55px;
  height: 55px;
  margin: auto;
  margin-left: -90px;
}

.best-works_wrap {
  display: flex;

  width: 650px;
  margin: 0 auto;
  margin-top: -31px;
}

.best-wok__slider {
  width: 100%;
  margin: 0 auto;
  margin-bottom: 55px;
}

.slick-list {
  width: 1200px;
  margin: 0 auto;
  padding: 0 60px;
}

.slick-slide img {
  display: block;

  margin: 0 auto;
}

.slick-prev:before,
.slick-next:before {
  font-size: 80px;
}

.slick-prev {
  left: 170px;
}

.slick-next {
  right: 170px;
}


.best-work__link {
  margin: 40px auto;
}

@media (max-width: 1281px) {
  .best-works {
    margin-top: -95px;
    background-color: #fff;
  }
}

@media (max-width: 800px) {
  .best-works {
    margin-top: -120px;
    background-color: #fff;
    width: 100%;

  }

  .best-works__title {
    padding-top: 40px;
  }

}

@media (max-width: 770px) {
  .work-item_text {
    width: 500px;
  }

  .best-works {
    margin-bottom: 20px;
  }
}

@media (max-width: 630px) {
  .best-works_wrap {
    margin-left: 70px;
  }

  .best-works {
    margin-top: -135px;
  }

  .best-works {
    margin-bottom: 15px;
  }
}

@media (max-width: 500px) {
  .best-works {
    display: none;
  }
}

/* слайдер от Мишки Owl Carousel 2 */
.owl-slider .owl-nav {
  position: absolute;
  top: 50%;
  margin-top: -75px !important;
  height: 0px;

  display: flex;
  justify-content: space-between;
  width: 100%;
}

a,
button {
  outline: none;
}

.owl-slider .owl-nav [class*=owl-] {
  opacity: 0.7;
  -webkit-transition: all .15s ease-in-out;
  -moz-transition: all .15s ease-in-out;
  -ms-transition: all .15s ease-in-out;
  -o-transition: all .15s ease-in-out;
  transition: all .15s ease-in-out;
}

.owl-slider .owl-nav [class*=owl-]:hover {
  opacity: 1;
  background: transparent !important;
}

.owl-slider .owl-prev {
  margin-left: 130px !important;
}

.owl-slider .owl-next {
  margin-right: 130px !important;
}

.owl-slider .owl-prev img {
  -webkit-transform: rotate(180deg);
  -moz-transform: rotate(180deg);
  -o-transform: rotate(180deg);
  -ms-transform: rotate(180deg);
  transform: rotate(180deg);
}

.owl-slider .owl-dots {
  margin-top: 20px;
}

@media (max-width: 1100px) {

  .work-item_text {
    padding: 31px 27px;
  }

  .work-item__link {
    margin-left: -210px;
  }
}

@media (max-width: 630px) {
  .work-item_text {
    padding: 20px 19px;
  }
}

@media (max-width: 600px) {
  .best-works_wrap {
    width: 100%;
  }

  .work-item_text {
    width: 100%;
    margin-left: 0px;
    padding-right: 100px;
    box-sizing: border-box;
  }

  .work-item__link {
    margin-left: -90px;
  }

  .owl-slider .owl-prev {
    margin-left: 10px !important;
  }

  .owl-slider .owl-next {
    margin-right: 10px !important;
  }
}

@media (max-width: 550px) {
  .best-works_wrap {
    width: 80%;
  }
}

@media (max-width: 400px) {
  .owl-slider .owl-nav {
    display: none;
  }
}
