/* стилизация серого блока с кнопкой заказа консультации*/
.call-to-action {
  display: flex;

  width: 357px;
}

.call-to-action__text {
  width: 250px;
  padding: 25px 30px;

  background-color: #fff;
}

@media (max-width: 500px) {
  .call-to-action {
    width: 100%;
  }
}

@media (max-width: 350px) {
  .call-to-action__text {
    padding: 18px 12px;
  }
}
