/* стилизация табов на странице Наши работы*/
.work-tabs {
  padding-bottom: 0;
}

.work-tabs__list {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  list-style: none;
  padding: 0;
  margin: 0;
}

.work-tabs__item {
  color: #fff;
  font-size: 14px;
  line-height: 18px;
  margin-bottom: 23px;
  margin-right: 16px;
}

.work-tabs__item a {
  font-family: 'Montserrat', sans-serif;
  font-size: 14px;
  line-height: 17px;
  color: #fff;
  font-weight: 600;
  display: block;
  text-decoration: none;
  padding: 15px 20px;

  border: 2px solid #ffffff;

}

.work-tabs__item a:hover,
.work-tabs__item a:active {
  border: 2px solid #fff;
  background-color: #ffffff;
  color: #000;
  font-weight: 600;
}

.work-tabs__wrap {
  max-width: 1010px;
}

/* миша abkmnhs фильтры на вкладках*/
.photos-works__item {
  position: unset !important;
}
.photos-works__list {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
}
@media (max-width: 600px) {
.photos-works__list {
    grid-template-columns: 1fr 1fr;
}
}
