/* стилизация блока "о компании"*/
.about-company {
  padding-top: 60px;
  padding-bottom: 5px;

  background-color: #ecd18b;
  background-image: url(../img/background-white-wood.png);
  background-repeat: repeat;
}

.about-company__list {
  margin: 0;
  padding: 0;

  list-style: none;
}

.advantages {
  display: flex;

  width: 100%;

  justify-content: center;
  flex-wrap: wrap;
}

.advantages__item {
  width: 21%;
  margin-bottom: 40px;
  padding: 0 15px;
}

.advantages__title {
  margin-bottom: 25px;
}


@media (max-width: 1040px) {
  .about-company {
    margin-top: -120px;
  }
}

@media (max-width: 800px) {
  .about-company {
    display: none;
  }

  .advantages__item {
    width: 20%;
    margin-bottom: 25px;
  }

  .advantages__title {
    margin-bottom: 15px;
    font-size: 12px;
    line-height: 15px;
  }

  .advantages__text {
    font-size: 12px;
    line-height: 17px;
  }
}

@media (max-width: 630px) {
  .advantages__item {
    width: 17%;
  }
}
