/* стилизация шапки сайта и первого блока главной страницы*/
.main-header {
  background-color: #63605d;
  // background-image: url(../img/background-ladder.png);
  background-image: url(../img/1393353746.jpg);
  background-repeat: no-repeat;
  background-size: cover;
}

.wrapper--top {
  max-width: 1170px;
  margin: 0 auto;
  padding: 0 15px;
  padding-top: 25px;
  padding-bottom: 120px;
}

.main-header__wrapper {
  display: flex;
  max-width: 430px;
  justify-content: space-between;
  align-items: center;
  max-width: 100%;

  background-color: #fff;
  margin-bottom: 65px;

}

.main-header__logo {
  max-width: 430px;
}

.header-logo {
  display: flex;

  margin-left: 16px;

  justify-content: space-around;
  align-items: center;
}

.header-logo__picture {
  max-width: 226px;
  padding: 0 10px;
}

.header-logo__description {
  max-width: 200px;
  font-size: 11px;
  line-height: 13px;
  margin: 0 20px;
}

.main-header__phone {
  max-width: 190px;
}

.phone {
  display: flex;
  justify-content: space-between;
}

.phone__number {
  position: relative;
  font-family: inherit;
  font-size: 16px;
  font-weight: 800;
  line-height: 20px;

  text-decoration: none;

  color: inherit;
  max-width: 190px;
  margin-bottom: 12px;
}

.phone__number::before {
  content: "";
  background-image: url('../img/whatsapp.svg');
  background-size: 28px 28px;
  height: 28px;
  width: 28px;
  position: absolute;
  top: 0;
  right: 100%;
  margin-right: 10px;
}

.phone__number::after {
  content: "Работает Whatsapp";
  font-size: 12px;
  position: absolute;
  top: 80%;
  left: 0;
  font-weight: 500;
}

.main-header__description {
  font-size: 20px;
  font-weight: 500;
  line-height: 32px;

  color: #fff;
}

.main-header__promo {
  display: flex;

  width: 100%;
  margin: 0 auto;
  padding: 0;
}

.achievements {
  font-family: inherit;
  font-weight: 500;

  padding-left: 175px;

  list-style: none;

  color: inherit;
}

.achievements__item {
  position: relative;

  margin-bottom: 60px;

  color: #fff;
}

.achievements__item::after {
  position: absolute;
  top: 0;
  left: -45px;

  width: 45px;
  height: 45px;

  content: '';

  background-image: url(../img/achievements__number-1.png);
  background-repeat: no-repeat;
}

.achievements__item--2::after {
  position: absolute;
  top: 0;
  left: -83px;

  width: 65px;
  height: 45px;

  content: '';

  background-image: url(../img/achievements__number-2.png);
  background-repeat: no-repeat;
}

.achievements__item--3::after {
  position: absolute;
  top: 0;
  left: -102px;

  width: 90px;
  height: 45px;

  content: '';

  background-image: url(../img/achievements__number-3.png);
  background-repeat: no-repeat;
}

.main-header__text {
  max-width: 860px;
}

.main-header__achievements {
  max-width: 340px;
  margin: 0;
  padding: 0;
}

.main-header__navigation {
  font-family: 'Montserrat', sans-serif;
  font-size: 16px;
  line-height: 22px;
  width: 130px;

  margin: 0;
  margin-right: -1px;
  padding: 0;
}

.call-to-action__button {
  width: 55px;
  height: 55px;
  margin: auto;
  margin-left: -30px;
}

.main-header__buttons {
  display: flex;
  justify-content: flex-end;
}

@media (max-width: 1281px) {
  .main-header__promo {
    margin-top: 90px;
  }

  .wrapper--top {
    padding-bottom: 40px;
  }

}

@media (max-width: 1175px) {
  .header-logo__description {
    display: none;
  }
}

@media (max-width: 1030px) {

  .header-logo__description,
  .main-header__achievements,
  .main-header__button {
    display: none;
  }

}

@media (max-width: 770px) {
  .main-header__promo--inner-page {
    margin-top: 25px;
  }
}

@media (max-width: 700px) {
  .phone__number {
    display: none;

  }

}

@media (max-width: 641px) {

  .main-header__promo {
    margin-top: 45px;
  }

  .main-header__wrapper {
    margin-left: 0;
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    z-index: 111;
  }

  .wrapper--top {
    margin-top: 30px;
  }

}

@media (max-width: 630px) {
  .wrapper--top {
    padding-bottom: 45px;
  }

}

@media (max-width: 575px) {
  .main-header__phone {
    display: none;
  }
}

@media (max-width: 550px) {
  .phone__number {
    font-size: 13px;
    line-height: 15px;
  }

  .call-to-action__button {
    margin-left: -75px;
  }

  .main-header__wrapper {
    margin-left: 0;
  }

  .wrapper--top {
    padding-left: 0;
    padding-right: 0;
    width: 95%;
  }

  .main-header__text {
    width: 100%;
  }

  .main-header__description {
    font-size: 13px;
    line-height: 20px;

  }
}

@media (max-width: 450px) {

  .header-logo {
    margin-left: 0;
  }

  .main-header__logo {
    padding: 12px 0;
  }

  .main-header__navigation {
    width: 60px;
  }

}
