/* мтилизация подвала сайта */
.footer {
  color: #eee8e2;
  background-color: #52301b;
}

.footer__top {
  display: flex;

  padding: 30px 0 20px;

  justify-content: space-around;
}

.footer__logo img {
  margin-bottom: 30px;
}

.footer__top-item {
  width: 30%;
  padding: 0 15px;
}

.footer__bottom {
  color: #908077;
  background-color: #402515;
}


.footer__text {
  padding-right: 70px;
}

.footer__title {
  font-size: 16px;
  font-weight: 700;
  line-height: 22px;

  margin-top: 0;
}

.footer__link {
  color: #cb895b;
}

.footer__contacts a {
  text-decoration: none;

  color: #eee8e2;
}

.footer__wrap {
  font-size: 12px;
  line-height: 18px;

  display: flex;

  padding-top: 16px;
  padding-bottom: 10px;

  justify-content: space-between;
  align-items: flex-start;
}

.footer-social__list {
  display: flex;

  margin: 0;
  padding: 0;

  list-style: none;
}

.footer-social__item {
  padding-left: 10px;
}

.footer__wrap p {
  width: 655px;
}

.footer__social {
  width: 280px;
  padding: 0;
  padding-left: 20px;
}

.footer__social span {
  display: block;

  margin-bottom: 10px;
}



@media (max-width: 1025px) {
  .footer__text {
    margin-top: 0;
    font-size: 12px;
    line-height: 15px;
  }

  .footer__logo img {
    margin-bottom: 10px;
  }

  .footer__link {
    font-size: 11px;
    line-height: 13px;
  }

  .footer__top {
    margin-bottom: 20px;
    padding-bottom: 0;
    font-size: 12px;
    line-height: 14px;
  }

}


@media (max-width: 770px) {
  .footer__logo img {
    margin-bottom: 5px;
  }

  .footer__text {
    padding-right: 20px;
    font-size: 11px;
    line-height: 13px;
    padding: 0;
    margin: 0;
  }

  .footer__top-item {
    padding-left: 0;
  }

  .footer__link {
    display: none;
  }

  .footer__wrap {
    padding-top: 5px;
    padding-bottom: 5px;
  }

  .footer__wrap p {
    width: 620px;
    margin: 0;
    font-size: 11px;
  }
}

@media (max-width: 630px) {
  .footer__logo img {
    width: 100%;
  }

}

@media (max-width: 500px) {
  .footer__top {
    display: block;
  }

  .footer__top-item {
    width: 85%;
    margin: auto;
  }

  .footer__logo img {
    width: 50%;
    margin: 0;
    padding: 0;
    padding-bottom: 10px;
  }

  .footer__wrap {
    display: block;
  }

  .footer__wrap p {
    display: none;
  }

  .footer__social {
    width: 90%;
    padding: 0;
    margin: auto;
  }
}
