/* стилизация блока на главной "с кем мы работаем" */
.who-we-work {
  padding-top: 52px;
  padding-bottom: 85px;

  background-image: url(../img/background-dark-wood.png);
  background-repeat: repeat-x;
  background-position: 0 0;
}

.who-we-work__wrapper {
  display: flex;
  justify-content: center;
  flex-direction: column;

}

.who-we-work__wrap {
  display: flex;
}

.who-we-work__text {
  width: 25%;
  margin: 0;
  padding: 0;
}

.who-we-work__list {
  display: flex;

  width: 75%;
  margin: 0;
  padding: 0;

  list-style: none;

  flex-wrap: wrap;
}

.who-we-work__title {
  margin-top: 70px;
  margin-bottom: 150px;
}

.who-we-work__description {
  font-size: 18px;
  line-height: 1.333;

  text-align: left;

  color: rgb(0, 0, 0);
}

.who-we-work__item {
  margin: -3px;
  padding: 0;
}

.who-we-work__link {
  display: inline-block;

  margin-top: 45px;
}

@media (max-width: 1921px) {
  .who-we-work__list {
    margin-top: 41px;
  }
}

@media (max-width: 1281px) {
  .who-we-work__link {
    margin-left: -6px;
    margin-top: -2px;
  }
}


@media (max-width: 1500px) {

  .who-we-work__item {
    width: 34%;
  }


  .who-we-work__item img {
    width: 100%;
  }

  .who-we-work__list {
    margin-top: 38px;
  }
}

@media (max-width: 1200px) {

  .who-we-work__title {
    margin-top: 40px;
    margin-bottom: 60px;
  }

  .who-we-work__description {
    color: #fff;
  }
}

@media (max-width: 800px) {
  .who-we-work__description {
    font-size: 16px;
    line-height: 20px;
  }

  .who-we-work {
    padding-top: 10px;
    padding-bottom: 30px;
  }

  .who-we-work__link {
    margin: 0 auto;
    margin-top: 20px;
  }
}

@media (max-width: 641px) {
  .who-we-work {
    padding-top: 0;
  }

  .who-we-work__description {
    font-size: 14px;
    line-height: 17px;
  }

  .who-we-work__link {
    display: inline-block;
    margin-top: 70px;
  }
}

@media (max-width: 630px) {
  .who-we-work__title {
    font-size: 17px;
    line-height: 20px;
  }

  .who-we-work__description {
    font-size: 13px;
    line-height: 16px;
  }

  .who-we-work__list {
    margin-top: 10px;
  }

  .who-we-work {
    padding-bottom: 55px;
  }
}

@media (max-width: 500px) {
  .who-we-work {
    display: none;
  }
}
