/* стилизация блока на главной "Прочие услуги"*/
.other-services {
  padding-bottom: 82px;

  background-image: url(../img/background-dark-wood--2.png);
  background-repeat: repeat-x;
  background-position: bottom 0 left 0;
}

.other-services__list {
  margin: 0;
  padding: 0;

  list-style: none;
}

@media (max-width: 1040px) {
  .other-services {
    padding-bottom: 201px;
  }
}

@media (max-width: 770px) {
  .other-services {
    padding: 10px 0;

  }

  .other-services__title {
    color: #fff;
  }
}

@media (max-width: 630px) {
  .other-services {
    padding-bottom: 120px;
  }

  .other-services__title {
    margin-bottom: 10px;
    padding-top: 10px;
    color: #fff;
  }
}

@media (max-width: 500px) {
  .other-services {
    padding-bottom: 15px;
    background-image: none;
  }

  .other-services__title {
    color: #000;
  }
}
