/* стилизация блока "Консультация эксперта"*/
.expert-advice {
  background-image: url(../img/background-white-wood.png);
  background-repeat: repeat-x;
  background-position: 0 0;
  padding: 77px;
}

.expert-advice__wrap {
  display: flex;
  align-content: center;

}

.expert-advice__text {
  flex: 1;
  margin-right: 100px;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
}

.expert-advice__button {
  max-width: 324px;
  padding: 22px 30px;
  margin: auto;
}

@media (max-width: 1050px) {
  .photo-work__wrap {
    max-width: 310px;
  }

  .photos-works__item {
    margin-bottom: 120px;
  }

  .photo-work__title {
    font-size: 15px;
    line-height: 18px;
  }
}

@media (max-width: 1024px) {
  .expert-advice {
    padding: 20px 35px;
  }
}


@media (max-width: 770px) {
  .expert-advice__wrap {
    display: block;
  }

  .photo-work__wrap {
    max-width: 230px;
  }

  .photos-works__item {
    margin-bottom: 95px;
  }

  .expert-advice {
    padding: 20px 35px;
  }
}

@media (max-width: 640px) {
  .photo-work__wrap {
    max-width: 200px;
  }

  .photos-works__item {
    margin-bottom: 45px;
  }

  .expert-advice {
    display: none;
  }
}

@media (max-width: 550px) {
  .photo-work__wrap {
    max-width: 170px;
  }

  .photo-work__title {
    font-size: 11px;
    line-height: 13px;
  }

  .photos-works__item {
    margin-bottom: 5px;
  }
}

@media (max-width: 360px) {
  .photo-work__wrap {
    max-width: 155px;
  }

  .photo-work__text {
    display: none;
  }

  .photo-work__title {
    font-size: 11px;
    line-height: 13px;
  }

  .photos-works__item {
    margin-bottom: 0;
  }

}
