/* глобальные стили */
body {
  font-family: 'Montserrat', sans-serif;
  font-size: 14px;
  font-style: normal;
  line-height: 20px;

  margin: 0;
  padding: 0;

  color: #000;
  background-color: #fff;
}

.visually-hidden:not(:focus):not(:active),
input[type='checkbox'].visually-hidden,
input[type='radio'].visually-hidden {
  position: absolute;

  overflow: hidden;
  clip: rect(0 0 0 0);

  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;

  white-space: nowrap;

  border: 0;

  clip-path: insert(100%);
}

.wrapper {
  max-width: 1170px;
  margin: 0 auto;
  padding: 0 15px;
}
