/* Все стили для кнопок */
.button {
  font-weight: 600;

  margin: 0 auto;
  padding: 23px 60px;

  cursor: pointer;
  text-decoration: none;

  color: #fff;
  border: none;
  background-color: #6691df;
}

.button--orange {
  font-weight: 600;

  max-width: 265px;
  margin: 0;
  padding: 26px;

  cursor: pointer;

  color: #fff;
  border: none;
  background-color: #cb895b;
}

.button--orange:hover {
  transition: all .3s ease-in-out;

  background-color: #b77b52;
}

.mini-button {
  padding: 6px;

  cursor: pointer;

  border: none;
  background-color: #6691df;
}

.mini-button--menu {
  font-weight: 600;

  position: relative;

  width: 130px;
  margin: 0 auto;
  padding: 12px;

  cursor: pointer;

  color: #fff;
  border: none;
  background-color: #6691df;
  display: flex;
  justify-content: center;
  align-items: center;
}

.mini-button--menu:hover,
.mini-button:hover,
.who-we-work__link:hover,
.best-work__link:hover {
  transition: all .3s ease-in-out;

  background-color: #587dc0;
}

.mini-button--search {
  font-weight: 600;

  width: 66px;
  margin: 0;

  cursor: pointer;

  color: #cb895b;
  border: none;
  background-color: #f2f2f2;
}

.mini-button--search img {
  width: 50%;
}

.phone__wrap {
  display: flex;
  flex-direction: column;

  margin-left: 10px;
}


@media (max-width: 1175px) {
  .button--orange {
    width: 220px;

  }
}

@media (max-width: 1025px) {
  .button--orange {
    width: 195px;
  }
}

@media (max-width: 550px) {
  .phone__wrap {
    display: none;
  }

  .mobile-hidden {
    position: absolute;

    overflow: hidden;
    clip: rect(0 0 0 0);

    width: 1px;
    height: 1px;
    margin: -1px;
    padding: 0;

    white-space: nowrap;

    border: 0;

    clip-path: insert(100%);
  }

  .mini-button--menu {
    width: 60px;
    height: 63px;
    margin-right: 0;
  }

  .main-header__buttons {
    position: absolute;
    display: block;
    right: 0;

  }

  .main-header__logo {
    padding: 14px;
    padding-left: 0;
  }

  .pop-up-form__button {
    padding: 17px 53px;
    width: 75%;
  }
}

@media (max-width: 450px) {
  .main-header__logo {
    padding: 12px;
  }

  .mini-button--menu {
    padding: 0;
  }
}
