/* стилизация хлебных крошек*/
.bredcrumbs {
  margin-bottom: 53px;
  display: flex;
  flex-wrap: wrap;
}

.bredcrumbs__text {
  color: #6180ba;
  font-family: inherit;
  font-size: 16px;
  font-weight: 600;
  padding: 6px;
}

.bredcrumbs__list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
}

.bredcrumbs__item {
  color: #fff;
  padding: 6px;

}

.bredcrumbs__item a {
  text-decoration: none;
  color: #6180ba;
  font-family: inherit;
  font-size: 16px;
  font-weight: 600;
  padding: 6px;
}

@media (max-width: 1024px) {
  .bredcrumbs {
    margin-top: -60px;
  }

  .bredcrumbs__text,
  .bredcrumbs__item,
  .bredcrumbs__item a {
    font-size: 13px;
    line-height: 15px;
  }



}
