/*стилизация Главного меню сайта*/
.top-navigation {
  position: relative;

}

.top-navigation__list {
  position: absolute;
  right: 0;

  display: flex;
  display: none;

  width: 1171px;
  margin: 0;
  padding: 5px 0;

  list-style: none;

  color: #000;
  background-color: #fff;

  justify-content: space-around;
  text-align: center;
}

.top-navigation__item a {
  font-size: 15px;
  font-weight: 700;
  line-height: 30px;

  text-decoration: none;
  text-transform: uppercase;
  display: block;
  padding: 10px 0;

  color: #000;
  background-color: #fff;
}

.top-navigation__item a:hover {
  transition: all .3s ease-in-out;

  color: #cb895b;
}

@media (max-width: 1230px) {
  .top-navigation__list {
    flex-direction: column;
    max-width: 400px;
    padding: 35px 60px;
    z-index: 11;
  }

  .top-navigation__item a {
    display: block;
    padding: 10px 0;
    border-bottom: 1px solid #e6e6e6;
  }

  .top-navigation__item a:hover,
  .top-navigation__item a:focus {
    border-bottom: 3px solid #cb895b;
    transition: all .3s ease-in-out;
  }

}

@media (max-width: 770px) {
  .top-navigation__item a {
    text-align: center;
  }

  .top-navigation__item:last-child a {
    border-bottom: none;
  }
}

@media (max-width: 550px) {
  .top-navigation__list {
    min-width: 320px;
  }

  .top-navigation__list {
    width: 171px;
    align-items: center;
  }

  .top-navigation__item a {
    display: block;
    padding: 6px 0;
    font-size: 13px;
    line-height: 25px;
  }

}
