/* стилизация блока на главной "Производство лестниц"*/
.manufacture-stairs {
  padding-bottom: 255px;

  background-image: url(../img/background-white-wood.png);
  background-repeat: repeat-x;
  background-position: bottom 0 left 0;
}

.manufacture-stairs__list,
.manufacture-fences__list {
  margin: 0;
  padding: 0;

  list-style: none;
}

.manufacture-fences {
  background-image: url(../img/background-white-wood.png);
  background-repeat: repeat-x;
  background-position: top 0 left 0;
}

.manufacture-fences {
  margin-top: -200px;
  padding-bottom: 124px;
}

@media (max-width: 1281px) {
  .manufacture-stairs {
    padding-bottom: 253px;
  }
}

@media (max-width: 1040px) {

  .manufacture-fences {
    margin-top: -307px;
  }

  .manufacture-fences {
    padding-bottom: 10px;
  }
}

@media (max-width: 500px) {

  .manufacture-fences,
  .manufacture-stairs {
    background-image: none;
  }
}
